@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body-wrapper{border:2px solid #707070;background:#e7f09c;border-radius:1rem;width:60%;margin-top:3rem}.body-wrapper .title_{text-align:center;font-family:Ubuntu;font-size:3rem;color:#606060}.body-wrapper .button_{color:#333;border:.1rem solid #707070;background-color:#fdf7f4;font-family:Ubuntu;font-size:1.1rem;font-weight:500;height:2rem}.body-wrapper .button_:focus{background-color:#fdf7f4}.MuiTableCell-root{font-size:.6rem !important}
